
import Link from '@mui/material/Link';
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useDispatch } from 'react-redux';

import { authActions } from 'store';
import { Button, Input, Typography } from '@mui/material';

export { Login };

function Login() {
    const dispatch = useDispatch();

    const validationSchema = Yup.object().shape({
        username: Yup.string().required('Username is required'),
        password: Yup.string().required('Password is required')
    });
    const formOptions = { resolver: yupResolver(validationSchema) };

    const { register, handleSubmit, formState } = useForm(formOptions);
    const { errors, isSubmitting } = formState;

    function onSubmit({ username, password }) {
        return dispatch(authActions.login({ username, password }));
    }

    return (
        <div className="card m-3">
            <h4 className="card-header">Login</h4>
            <div className="card-body">
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div className="mb-3">
                        <Typography className="form-label">Username</Typography>
                        <Input name="username" type="text" {...register('username')} className={`form-control ${errors.username ? 'is-invalid' : ''}`} placeholder="Type in here" variant="outlined" />
                       <div className="invalid-feedback">{errors.username?.message}</div>
                    </div>
                    <div className="mb-3">
                        <Typography className="form-label">Password</Typography>
                        <Input  name="password" type="password" {...register('password')} className={`form-control ${errors.password ? 'is-invalid' : ''}`} placeholder="Type in here" variant="outlined" />
                       <div className="invalid-feedback">{errors.password?.message}</div>
                    </div>
                    <Button type="submit" >
                        Login   
</Button>
                   
                    <Link href="./register" >Register</Link>
                </form>
            </div>
        </div>
    )
}
